import axios from 'axios'

// å¯ä»¥åœ¨è¿™é‡Œåšä¸€ä¸‹è¯·æ±‚æ‹¦æˆªï¼Œè®¾ç½®å…¬å…±è¯·æ±‚å¤´ç­‰

export default function ajax(url:string, data:{}, type:string = 'POST') {
    
    // åˆ¤æ–­ url åœ°å€ï¼Œ åœ¨å¤šä¸ªåŽå°åœ°å€æ—¶å€™ä½¿ç”¨ï¼Œ å¯ä»¥åœ¨è¿™é‡Œæ‹¦æˆªä½¿ç”¨ä¸åŒçš„è¯·æ±‚å¤´ï¼Œä¼ å…¥ä¸åŒtokenç­‰æ“ä½œ
    
    return new Promise((resolve, reject) => {
        let promise:any // è¿”å›žä¸€ä¸ª promise å¯¹è±¡
        if (type === 'GET') {
            promise = axios.get(url)
            
            // å¤šä¸ªåŽå°åœ°å€æ—¶å€™ï¼Œä¼ å…¥ä¸åŒçš„tokenå€¼
            promise = axios.get(url, { params : data, headers: {} })
        } else if (type === 'POST') {
            promise = axios.post(url, data)
            
            // å¤šä¸ªåŽå°åœ°å€æ—¶å€™ï¼Œä¼ å…¥ä¸åŒçš„tokenå€¼
            promise = axios.get(url, data)
        }
        // ç»Ÿä¸€å¤„ç† response
        promise.then((response:any) => {
           (response && response.data) ? resolve(response.data) : reject(response)
        }).catch((error:any) => {
          console.log(error)
        })
    })
}